import {
  Text,
  Flex,
  Th,
  Image,
  Spinner,
  Stat,
  StatHelpText,
  StatArrow,
  useToast,
  useClipboard,
} from "@chakra-ui/react";
import { formatPrice, getAssetImg, greaterThanMax } from "../helpers";

import { useStyles } from "../hooks/useStyles";
import { SETTINGS } from "../helpers/settings";

export const TableHead = ({ children, simple }) => {
  if (!children) return null;

  return (
    <Thh simple={simple}>
      {children
        .toString()
        .split(" ")
        .map((w, i) => {
          return <Text key={i}>{w}</Text>;
        })}
    </Thh>
  );
};

export const AssetCell = ({ mainnet, name }) => {
  const { onCopy } = useClipboard(mainnet);
  const toast = useToast();

  const copyAddress = () => {
    onCopy();
    toast({
      title: `Token address copied`,
      ...SETTINGS.toast,
    });
  };
  return (
    <Flex align="center" h="12" onClick={copyAddress}>
      <Image
        src={getAssetImg(mainnet)}
        alt={name}
        boxSize="30px"
        fallbackSrc={require("../assets/images/ph_tk.png").default}
      />
      <Text pl={2} fontSize="lg">
        {name}
      </Text>
    </Flex>
  );
};

export const ShowValue = ({ value, price }) => {
  if (value === "0" || value === 0 || value === "0.00")
    return <TextValue>-</TextValue>;
  if (!value)
    return (
      <TextValue>
        <Spinner size="sm" />
      </TextValue>
    );
  return <TextValue>{price ? `$ ${formatPrice(value)}` : value}</TextValue>;
};

export const TextValue = ({ children }) => {
  return (
    <Text fontWeight="500" pl={2} fontSize="lg" minW="120px">
      {children}
    </Text>
  );
};

export const Thh = ({ children, simple }) => {
  const { buttonBorderStyle, colorSubtitle } = useStyles();

  return (
    <Th
      isNumeric={simple ? false : true}
      borderBottom={buttonBorderStyle}
      color={colorSubtitle}
    >
      {children}
    </Th>
  );
};

export const GainsValue = ({ totalGainsUSD, isJoined }) => {
  if (isJoined === "0") return "-";
  if (!totalGainsUSD || totalGainsUSD === "0.00")
    return (
      <Stat>
        <StatHelpText m="0">$0.00</StatHelpText>
      </Stat>
    );
  const positive = greaterThanMax(totalGainsUSD, "0");
  return (
    <Stat>
      <StatHelpText m="0">
        <Flex justify="flex-start" align="center">
          <StatArrow type={positive ? "increase" : "decrease"} />$
          {totalGainsUSD}
        </Flex>
      </StatHelpText>
    </Stat>
  );
};
