import { ERC20, useContractCalls, useEthers } from "@usedapp/core";
import { Interface } from "@ethersproject/abi";
import { useMemo } from "react";
import { useAllTokens } from "./useAllTokens";
import { useManagerState } from "../context/managerContext";

export function useTokensBalance(agreementAddress = null) {
  const { chainId } = useEthers();
  const { portfolioSelected } = useManagerState();
  const portfolioAddress = agreementAddress ? agreementAddress : portfolioSelected?.address
  const allTokens = useAllTokens();

  const ERC20Interface = new Interface(ERC20.abi);

  const results = useContractCalls(
    allTokens && portfolioAddress
      ? allTokens.map((t) => ({
          abi: ERC20Interface,
          address: t[chainId],
          method: "balanceOf",
          args: [portfolioAddress],
        }))
      : []
  );
  return useMemo(() => {
    let balances = {};
    allTokens.forEach(
      (token, idx) =>
        (balances[token[chainId]] = results[idx] && results[idx][0])
    );
    console.log('useTokensBalance',portfolioAddress, balances, results, 
    // allTokens.map(t => ({
    //   address: t[chainId],
    //   method: 'balanceOf'
    // }))
    )
    return balances;
  }, [results]); // eslint-disable-line
}
