import { useEffect } from "react";
import {
  Text,
  Flex,
  Thead,
  Tbody,
  Tr,
  Th,
  Td,
  Spinner,
  Button,
} from "@chakra-ui/react";
import useUSDCPrice from "../hooks/useUSDCPrice";
import { useTokensBalance } from "../hooks/useTokensBalance";
import {
  formatBNPrice,
  formatBNUnits,
  getBalanceByToken,
  isAddress,
  subBN,
  sumBN,
} from "../helpers";
import { useManagerDispatch } from "../context/managerContext";
import {
  useTransferToAgreementModalToggle,
  useWithdrawFromVaultModalToggle,
} from "../hooks";
import { useTranslation } from "react-i18next";
import { ChainId, useEthers } from "@usedapp/core";
import useStrategiesBalance from "../hooks/useStrategiesBalance";
import { useStyles } from "../hooks/useStyles";
import useVaultBalance from "../hooks/useVaultBalance";
import { AiOutlineDownload, AiOutlineExport } from "react-icons/ai";
import { isProduction } from "../helpers/enviroment";
import { useMainTokens } from "../hooks/useMainTokens";
import { AssetCell, ShowValue, TableHead, Thh } from "./TableCommon";

export function PortfolioTable({ agreementAddress }) {
  const { t } = useTranslation();
  const { buttonBorderStyle } = useStyles();

  const { chainId } = useEthers();
  const mainTokens = useMainTokens();
  const tokensBalance = useTokensBalance(agreementAddress);
  const strategiesBalance = useStrategiesBalance(agreementAddress);
  const { data: vaultBalance, isLoading: vaultBalanceLoading } =
    useVaultBalance();

  const isLoading = strategiesBalance.isLoading && vaultBalanceLoading;
  const isReadOnly = isAddress(agreementAddress);

  return (
    <>
      <Thead>
        <Tr>
          <Th borderBottom={buttonBorderStyle}>
            <Text color="white">PORTFOLIO</Text>
          </Th>
          <TableHead>{t("portfolioAllocation")}</TableHead>
          <TableHead>{t("totalUsd")}</TableHead>
          <Thh></Thh>
        </Tr>
      </Thead>
      <Tbody>
        {mainTokens &&
          mainTokens.map((token) => {
            const tk = isAddress(token[chainId]);
            return (
              <TableRow
                key={token[chainId]}
                token={token}
                isReadOnly={isReadOnly}
                agreementBalance={tokensBalance && tokensBalance[tk]}
                vaultBalance={
                  vaultBalance && getBalanceByToken(vaultBalance, tk)
                }
                strategyBalance={
                  strategiesBalance && strategiesBalance.holdings[tk]
                }
                strategyInvested={
                  strategiesBalance && strategiesBalance.invested[tk]
                }
                strategySumApy={
                  strategiesBalance && strategiesBalance.sumApy[tk]
                }
                isLoading={isLoading}
                chainId={chainId}
              />
            );
          })}
      </Tbody>
    </>
  );
}

function TableRow({
  token,
  agreementBalance,
  vaultBalance,
  strategyBalance,
  strategyInvested,
  strategySumApy,
  isLoading,
  chainId,
  isReadOnly,
}) {
  const { t } = useTranslation();
  const { buttonBorderStyle, bgHover, colorTitle } = useStyles();
  const { name, decimals } = token;

  const agreementTotal = formatBNPrice(agreementBalance, decimals);
  const vaultBalanceAccount = vaultBalance
    ? formatBNPrice(vaultBalance, decimals)
    : "0"; // balance from the vault for this pair token/account
  const portfolioBalance = sumBN(agreementTotal, vaultBalanceAccount);
  const totalAssetsStrategy = formatBNPrice(strategyBalance, decimals);
  const gainsStrategies = subBN(strategyBalance, strategyInvested);
  const totalGainsStrategy = formatBNUnits(gainsStrategies, decimals);

  const totalSum = sumBN(portfolioBalance, totalAssetsStrategy);
  const totalUSD = useUSDCPrice(token[1], totalSum); // hardcode mainnet to get assetPrice
  const totalUSDPortfolio = useUSDCPrice(token[1], portfolioBalance); // hardcode mainnet to get assetPrice
  const totalGainsUSD = useUSDCPrice(token[1], totalGainsStrategy); // hardcode mainnet to get assetPrice

  const { updatePortfolioBalance } = useManagerDispatch();
  const withdrawFromVaultModalToggle = useWithdrawFromVaultModalToggle(
    token[chainId]
  );

  const transferToAgreementModalToggle = useTransferToAgreementModalToggle(
    token[chainId]
  );

  useEffect(() => {
    updatePortfolioBalance({
      address: token[chainId],
      total: totalUSD,
      gains: totalGainsUSD,
      sumApy: strategySumApy,
    });
    // eslint-disable-next-line
  }, [
    totalUSD,
    totalGainsUSD,
    token,
    chainId,
    updatePortfolioBalance,
    strategySumApy,
  ]);

  return (
    <Tr
      key={token[chainId]}
      _hover={{
        bg: bgHover,
      }}
    >
      <Td borderBottom={buttonBorderStyle}>
        <AssetCell name={name} mainnet={token[ChainId.Mainnet]} />
      </Td>

      {/* PORTFOLIO ALLOCATION */}
      <Td isNumeric borderBottom={buttonBorderStyle}>
        <ShowValue value={portfolioBalance} /> {/* TODO: REMOVE DEBUG */}
        {/* DEBUG BALANCE */}
        {!isProduction() && (
          <Text color="orange">
            ({agreementTotal} + {vaultBalanceAccount})
          </Text>
        )}
      </Td>

      {/* TOTAL USD */}
      <Td isNumeric borderBottom={buttonBorderStyle}>
        <ShowValue value={totalUSDPortfolio} price={true} />
      </Td>

      <Td borderBottom={buttonBorderStyle}>
        {!isReadOnly &&
          (isLoading ? (
            <Spinner size="sm" />
          ) : (
            <>
              <Flex color={colorTitle} justify="space-evenly">
                <Button
                  variant="ghost"
                  px="2"
                  onClick={() => transferToAgreementModalToggle()}
                >
                  <Text fontWeight="600" pl={2} fontSize="sm" mr="1">
                    {t("deposit")}
                  </Text>
                  <AiOutlineDownload />
                </Button>

                <Button
                  variant="ghost"
                  px="2"
                  onClick={() => withdrawFromVaultModalToggle()}
                >
                  <Text fontWeight="600" pl={2} fontSize="sm" mr="1">
                    {t("withdraw")}
                  </Text>
                  <AiOutlineExport />
                </Button>
              </Flex>
            </>
          ))}
      </Td>
    </Tr>
  );
}
