import { isTheSameAddress } from "../helpers";
import useStrategiesFromAccount from "./useStrategiesFromAccount";

export default function useStrategyAgreementHoldings(
  strategyAddress,
  agreementAddress = null
) {
  const { data: accountData, isLoading } =
    useStrategiesFromAccount(agreementAddress);

  return {
    data: accountData?.strategies?.find((st) =>
      isTheSameAddress(st.strategy.id, strategyAddress)
    ),
    isLoading: isLoading,
  };
}
