import { Box, Flex } from "@chakra-ui/react";

import { useParams } from "react-router-dom";
import { isAddress } from "@ethersproject/address";
import { MainOverviewTable } from "./MainOverviewTable";
import { PortfolioLine } from "./common/PortfolioLine";

function OverviewOnly() {
  // Add portfolio directly from URL
  const { address } = useParams();
  const agreementAddress = isAddress(address);
  return (
    <>
      {!agreementAddress ? (
        "invalid agreement"
      ) : (
        <Box w="full">
          <Flex p="6">
            <PortfolioLine address={address} />
          </Flex>

          <Flex mt="12" overflowX="scroll">
            <MainOverviewTable agreementAddress={address} />
          </Flex>
        </Box>
      )}
    </>
  );
}

export default OverviewOnly;
