import { useMemo } from "react";
import {
  // formatBNPrice,
  // formatPercentage,
  // getApy,
  getHoldings,
  // getTokenDecimals,
  isAddress,
  isTheSameAddress,
  mulBN,
  // mulBN,
  sumBN,
} from "../helpers";
import useStrategiesFromAccount from "./useStrategiesFromAccount";
import { useEthers } from "@usedapp/core";

const useStrategiesBalance = (agreementAddress = null) => {
  const { data: accountData, isLoading } = useStrategiesFromAccount(agreementAddress);
  const { chainId } = useEthers();

  return useMemo(() => {
    let totalInvested = {};
    let totalBalance = {};
    // let totalSumApy = {};

    accountData?.strategies?.forEach(async (st) => {
      const tk = isAddress(st?.strategy?.token?.id);
      // const tokenInfo = getTokenDecimals(tk, chainId);
      const fixFactor =  isTheSameAddress(st?.strategy?.id, '0x6d79EB287d58Bbf5bf2B7D4F9847fb44068d61dD') ? '1.00134361261301729858' : '1' 

      const holdings = getHoldings(
        st?.shares,
        st?.strategy?.shares,
        mulBN(st?.strategy?.deposited, fixFactor)
      );

      // calc APY
      // const lengthRate = st?.strategy?.rates?.length;
      // const initialRate = st?.strategy?.rates ? st?.strategy?.rates[1] : 0;
      // const finalRate = st?.strategy?.rates
      //   ? st?.strategy?.rates[lengthRate - 1]
      //   : 0;
      // const apy = formatPercentage(getApy(initialRate, finalRate));

      //  (APYGS1 * USDI1 + APYGS2 * USDI2 + APYGS3 * USDI3) / (USDI1 + USDI2 + USDI3)
      // const apyPerAmount = mulBN(
      //   apy,
      //   formatBNPrice(holdings, tokenInfo.decimals)
      // );

      // console.log("useStrategiesBalance ST", st?.strategy?.id, holdings, st?.invested, tokenInfo.name, tk, totalInvested.hasOwnProperty(tk));

      if (totalInvested.hasOwnProperty(tk)) {
        totalInvested[tk] = sumBN(totalInvested[tk], st?.invested);
        totalBalance[tk] = sumBN(totalBalance[tk], holdings);
        // totalSumApy[tk] = sumBN(totalSumApy[tk], apyPerAmount);
      } else {
        totalInvested[tk] = st?.invested;
        totalBalance[tk] = holdings;
        // totalSumApy[tk] = apyPerAmount;
      }
    });
    
    // console.log("useStrategiesBalance totalBalance", totalBalance);
    // console.log("useStrategiesBalance totalInvested",totalInvested);

    return {
      invested: totalInvested,
      holdings: totalBalance,
      sumApy: 0,
      isLoading: isLoading,
    };
    // eslint-disable-next-line
  }, [accountData, chainId, isLoading]);
};

export default useStrategiesBalance;
